<template>
	<div id="navbar" class="p-4 md:p-6 bg-white shadow  md:fixed w-full z-10 transition-colors duration-300"
	:class="{'md:bg-transparent md:shadow-none' : !showWhiteBg}">
		<div class="container mx-auto flex flex-col md:flex-row items-center justify-between">
			<div class=" w-full flex flex-row justify-between items-center">
				<div @click="$router.push('/')" class="text-3xl font-bold cursor-pointer">
					<span class="text-gray-800" :class="{ 'md:text-white' : !showWhiteBg }">el</span>
					<span class="text-primary">Blog</span>
				</div>
				<div>
					<button v-show="!isVisible" class="md:hidden" @click="toggle">
						<svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16"></path></svg>
					</button>
					<button v-show="isVisible" class="md:hidden" @click="toggle">
						<svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12"></path></svg>
					</button>

				</div>
			</div>
			<div class="mt-4 md:mt-0 text-gray-700 w-full md:space-x-5 md:block md:text-right" :class="{ 'hidden' : !isVisible, 'md:text-white' : !showWhiteBg }">
				<NavbarLink to='/articles' label='Articles'/>
				<NavbarLink to='/about' label='About'/>
				<NavbarLink to='/contact' label='Contact'/>
			</div>
		</div>
	</div>
</template>

<script>
import { useToggle } from '@/composables/useToggle'
import { ref } from 'vue'
import NavbarLink from './NavbarLink.vue'

export default {
    components: {
		NavbarLink
	},
	setup() {
		let { isVisible, toggle } = useToggle()
		let showWhiteBg = ref(false)

		document.addEventListener("scroll", function() {
			let topPosition = document.body.getBoundingClientRect().top

			if (topPosition < -150) {
				showWhiteBg.value = true
			} else {
				showWhiteBg.value = false
			}
		})

		return {
			isVisible,
			toggle,
			showWhiteBg
		}
	}
}
</script>