<template>
    <div>
        <div :class="{'space-y-6' : isList, 'grid sm:grid-cols-2  md:grid-cols-3 sm:gap-6 lg:gap-12 md:mx-4 lg:mx-0' : isGrid}">
            <Article :layout="layout"/>
            <Article :layout="layout"/>
            <Article :layout="layout"/>
            <Article :layout="layout"/>
            <Article :layout="layout"/>
        </div>
        
        <div v-if="showViewAll" class="mt-8 md:mt-12">
            <router-link to="/articles" class=" mx-4 px-4 py-2 md:px-6 bg-primary text-white focus:outline-none rounded">View all</router-link>
        </div>
    </div>
</template>

<script>
import { computed } from '@vue/reactivity'
import Article from './Article.vue'
export default {
    components: { Article },
    props: {
        layout: {
            type: String,
            default: 'list'
        },
        showViewAll: {
            type: Boolean,
            default: true
        }
    },
    setup(props) {
		let isGrid = computed(() => {
			return props.layout === 'grid'
		}) 
		let isList = computed(() => {
			return props.layout === 'list'
		}) 
		return {
			isGrid,
			isList
		}
	}
}
</script>

